/* GLOBAL STYLES */
:root {
  --primary-color: #fff063;
  --highlight-color: #c59dd3;
  --dark-color: #000101;
  --light-color: #fee1e3;
  --danger-color: #ff87c5;
  --success-color: #06ad59;
}

/* BASE STYLES */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  background-color: #ffb6c129;
}
#root {
  background-color: #ffb6c129;
}

text {
  font-family: 'Inconsolata', monospace;
}
body {
  font-family: 'Inconsolata', monospace;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #fff;
  color: #333;
}

a {
  color: var(--highlight-color);
  text-decoration: none;
}

ul {
  list-style: none;
}

img {
  width: 100%;
}

.container {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  padding: 0 2rem;
  margin-top: 6rem;
  margin-bottom: 3rem;
}

.landing {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 4fr 2fr;
  background: url('./img/hero.png') no-repeat center/cover;
  height: 100vh;
}

.container-service {
  position: relative;
  display: grid;
  grid-template-columns: 5fr 1fr;
  height: 70vh;
  margin: 0;
  background: url('./img/services-hero-final.png') no-repeat center/cover;
}

.container-resource {
  position: relative;
  display: grid;
  grid-template-columns: 4fr 1fr;
  height: 60vh;
  margin: 0;
  background: url('./img/resources-hero-final4.png') no-repeat center/cover;
}

/*  TEXT STYLES */
.x-large {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.large {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.medium {
  font-size: 2.5rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.lead {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  width: 40%;
  text-align: justify;
}

.lead-profile-top {
  font-size: 1.2rem;
  margin-bottom: 1rem;
  width: 40%;
  text-align: center;
}

.lead-profile {
  font-size: 1.4rem;
  width: 100%;
  margin: 1.2rem 0;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: var(--dark-color);
}

.text-dark {
  color: var(--dark-color);
}

.icon-dashboard {
  color: var(--light-color);
}
/* PADDING SHORTCUTS ==> BASED ON BOOTSTRAP CLASSES */
.p {
  padding: 0.5rem;
}
.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 2rem;
}
.p-3 {
  padding: 3rem;
}
.py {
  padding: 0.5rem 0;
}
.py-1 {
  padding: 1rem 0;
}
.py-2 {
  padding: 2rem 0;
}
.py-3 {
  padding: 3rem 0;
}

/* MARGIN SHORTCUTS ==> BASED ON BOOTSTRAP CLASSES */
.m {
  margin: 0.5rem;
}
.m-1 {
  margin: 1rem;
}
.m-2 {
  margin: 2rem;
}
.m-3 {
  margin: 3rem;
}
.my {
  margin: 0.5rem 0;
}
.my-1 {
  margin: 1rem 0;
}
.my-2 {
  margin: 2rem 0;
}
.my-3 {
  margin: 3rem 0;
}

/* BUTTON STYLES */
.btn {
  display: inline-block;
  background: var(--dark-color);
  color: #333;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
  border-radius: 20px;
}

.btn-auth {
  display: inline-block;
  background: var(--dark-color);
  color: #333;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
}

.btn-connect {
  border-radius: 20px;
  background: var(--danger-color);
}

.badge {
  font-size: 0.8rem;
  padding: 0.1rem;
  text-align: center;
  margin: 0.3rem;
  background: var(--light-color);
  color: #333;
}

.alert {
  padding: 0.8rem;
  margin: 1rem 0;
  opacity: 0.9;
  background: var(--light-color);
  color: #333;
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: var(--dark-color);
  color: #fff;
}

.btn-nav {
  background: var(--dark-color);
  color: #fff !important;
  padding: 0.6rem 1.5rem !important;
  font-size: 1rem;
  cursor: pointer;
  transition: opacity 0.2s ease-in !important;
}

.btn-nav-member {
  background: none;
  color: black !important;
}

.btn-light,
.bg-light,
.badge-light,
.alert-light {
  background: var(--light-color);
  color: #333;
}

.nav-primary,
.badge-dark,
.alert-dark {
  background: var(--primary-color);
  color: #fff;
}

.btn-dark {
  background: var(--dark-color);
  color: #fff;
}

.badge-highlight {
  width: 100%;
  background: var(--highlight-color);
  color: #fff;
}

.badge-highlight {
  background: var(--highlight-color);
  color: #fff;
}
.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: var(--danger-color);
  color: #fff;
}

.btn-success,
.bg-success,
.badge-success,
.alert-success {
  background: var(--success-color);
  color: #fff;
}

.btn-white,
.bg-white,
.badge-white,
.alert-white {
  background: #fff;
  color: #333;
}

.btn:hover {
  opacity: 0.8;
}

.round-img {
  border-radius: 50%;
}

.line {
  height: 1px;
  background: #ccc;
  margin: 1.5rem 0;
}

