/* FORM STYLES  */

.join-hero {
  max-width: 1100px;
  position: relative;
  margin-top: -25px;
  overflow: hidden;
  padding: 0 2rem;
  margin-top: 6rem;
  margin-bottom: 3rem;
  background: url('./img/login-hero.png') center/cover;
  height: 50vh;
}

.form .form-group {
  margin: 1.2rem 0;
}

.form .form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

.form input[type='text'],
.form input[type='email'],
.form input[type='password'],
.form input[type='date'],
.form select {
  display: block;
  width: 40%;
  padding: 0.4rem;
  font-size: 0.9rem;
  border: 1px solid #ccc;
}

.textarea-posts {
  display: block;
  width: 100%;
  padding: 0.4rem;
  font-size: 0.9rem;
  border: 1px solid #c59dd3;
}

.form-profile input[type='text'],
.form-profile input[type='email'],
.form-profile input[type='password'],
.form-profile input[type='date'],
.form-profile select,
.form-profile textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  font-size: 0.9rem;
  border: 1px solid #ccc;
}

.form input[type='submit'],
button {
  font: inherit;
}

.form .social-input {
  display: flex;
}

.form .social-input i {
  padding: 0.5rem;
  width: 4rem;
}

.table th,
.table td {
  padding: 1rem;
  text-align: left;
}

.table th {
  background: var(--light-color);
}

/* NAVBAR STYLES */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 2rem;
  position: fixed;
  z-index: 1;
  width: 100%;
  top: 0;
}

.navbar-logo {
  font-size: 2rem;
  font-weight: 600;
}
.navbar ul {
  display: flex;
}

.navbar a {
  color: var(--dark-color);
  padding: 0.45rem;
  margin: 0 0.25rem;
}

.navbar a:hover {
  color: var(--highlight-color);
}

.navbar .welcome span {
  margin-right: 0.6rem;
}

/* LANDING PAGE STYLES */
.landing {
  position: relative;
  display: grid;
  grid-template-columns: 2fr 4fr 2fr;
  background: url('./img/hero.png') no-repeat center/cover;
  height: 100vh;
}

.landing-inner {
  color: black;
  height: 100%;
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.services-inner {
  color: black;
  height: 100%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: center;
  text-align: left;
}

/* PROFILE PAGE STYLES */
.profile {
  display: grid;
  grid-template-columns: 1fr 2fr 2fr;
  align-items: center;
  grid-gap: 2rem;
  padding: 1rem;
  line-height: 1.8;
  margin-top: 1rem;
  margin-bottom: 1rem;
  border-radius: 20px;
}

.profile-grid {
  display: grid;
  grid-template-areas:
    'top top'
    'about about'
    'exp edu'
    'github github';
  grid-gap: 1rem;
  border-radius: 20px;
}

.profile-top {
  grid-area: top;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 20px;
  background-color: var(--highlight-color);
}

.profile-top img {
  width: 250px;
}

.profile-top .icons a {
  color: var(--light-color);
  margin: 0 0.3rem;
}

.profile-top .icons a:hover {
  color: var(--dark-color);
}

.profile-about {
  grid-area: about;
  text-align: center;
  border-radius: 20px;
}

.profile-about .skills {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.profile-exp {
  grid-area: exp;
  border-radius: 20px;
}

.profile-edu {
  grid-area: edu;
  border-radius: 20px;
}

.profile-exp h2,
.profile-edu h2 {
  margin-bottom: 1rem;
}

.profile-exp > div,
.profile-edu > div {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: #ccc 1px dashed;
}

.profile-exp > div:last-child,
.profile-edu > div:last-child {
  border: 0;
}

.profile-exp p,
.profile-edu p {
  margin: 0.5rem 0;
}

.profile-github {
  grid-area: github;
}

.profile-github .repo {
  display: flex;
  border-radius: 20px;
}

.profile-github .repo > div:first-child {
  flex: 7;
  flex-basis: 70%;
}

.profile-github > div:last-child {
  flex: 3;
  flex-basis: 20%;
}

/* FORUM PAGE STYLES */
.post-form .post-form-header {
  background: var(--highlight-color);
  padding: 0.5rem;
}

.post {
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-gap: 2rem;
  align-items: center;
  border-radius: 20px;
}

.post > div:first-child {
  text-align: center;
}

.post img {
  width: 100px;
}

.post .comment-count {
  background: var(--light-color);
  color: var(--dark-color);
  padding: 0.1rem 0.2rem;
  border-radius: 5px;
  font-size: 0.8rem;
}

.post .post-date {
  color: #aaa;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

/* MOBILE RESPONSIVE STYLES */
@media (max-width: 700px) {
  .container {
    margin-top: 10rem;
  }

  .hide-sm {
    display: none;
  }

  .join-hero {
    background: none;
  }
  .landing {
    background: none;
    background-color: var(--light-color);
  }
  .container-service {
    background: none;
  }

  .container-resource {
    background: none;
  }
  /* TEXT STYLES */
  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1rem;
    width: 90%;
  }

  .meduim {
    font-size: 0.8rem;
    width: 100%;
  }
  .lead-profile {
    font-size: 1.2rem;
    width: 100%;
    text-align: left;
  }

  .landing-inner {
    color: black;
    height: 100%;
    width: 100%;
    margin: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
  /* NAVBAR STYLES */
  .navbar {
    display: block;
    text-align: center;
  }

  .navbar ul {
    text-align: center;
    justify-content: center;
  }

  .navbar h1 {
    margin-bottom: 1rem;
  }

  .navbar .welcome {
    display: none;
  }

  /* FORM STYLES */
  .form input[type='text'],
  .form input[type='email'],
  .form input[type='password'],
  .form input[type='date'],
  .form select,
  .form textarea {
    display: block;
    width: 100%;
    padding: 0.4rem;
    font-size: 1.2rem;
    border: 1px solid #ccc;
  }

  .textarea-posts {
    display: block;
    width: 40%;
    padding: 0.4rem;
    font-size: 0.9rem;
    border: 1px solid #c59dd3;
  }

  /* PROFILE PAGE STYLES */
  .profile {
    grid-template-columns: 1fr;
    text-align: center;
    border-radius: 20px;
  }

  .profile ul {
    display: none;
  }

  .profile-top img,
  .profile img {
    width: 200px;
    margin: auto;
  }

  .profile-grid {
    grid-template-areas:
      'top'
      'about'
      'exp'
      'edu'
      'github';
  }

  .profile-about .skills {
    flex-direction: column;
  }

  .dash-buttons a {
    display: block;
    width: 100%;
    margin-bottom: 0.2rem;
  }

  .post {
    grid-template-columns: 1fr;
  }
  .post a,
  .post button {
    padding: 0.3rem 0.4rem;
  }
}
